.slider__container {
  position: relative;
  display: flex;
  justify-content: center;
}

.slider__slider {
  margin-left: 30px;
  margin-right: 10px;
  width: 60%;
}

span [class^="MuiSlider-markLabel"] {
  font-weight: bold;
  color: #AEACFF;
  transform: rotate(0deg) translateX(-50%) translateY(-45px);
}

span [class^="MuiSlider-thumb WithStyles(ForwardRef(Slider))-thumb-9 MuiSlider-thumbColorPrimary"] {
  color: #5551FF;
  background-color: #5551FF;
}

.MuiSlider-mark {
  width: 6.34px !important;
  height: 6px !important;
  position: absolute !important;
  border-radius: 4px !important;
  background-color: #AEACFF !important;
}

.MuiSlider-track {
  background-color: inherit !important;
}

.WithStyles\(ForwardRef\(Slider\)\)-track-4 {
  border: none !important;
}

.WithStyles\(ForwardRef\(Slider\)\)-rail-5 {
  color: #5551FF30;
  height: 2px !important;
  margin-top: 1px !important;
  border: 2px solid #AEACFF !important;
  /* border-radius: 4px; */
}

.WithStyles\(ForwardRef\(Slider\)\)-track-4 {
  color: #5551FF50;
  height: 4px;
  margin-top: 1px;
  border: 2px solid #AEACFF;
  /* border-radius: 4px; */
}

.WithStyles\(ForwardRef\(Slider\)\)-thumb-2 {
  width: 21px !important;
  border: none !important;
  height: 20px !important;
  margin-top: -8px !important;
  margin-left: -12px !important;
  background-color: #5551FF !important;
}
